.modal-background {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: #1c1b1bd9;
	animation: fadeIn 0.3s;
}

.modal-foreground {
	font-family: 'Cinzel', serif;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	height: 300px;
	width: 400px;
	background-color: #464962;
	border-radius: 10px;
	display: none;
	flex-direction: column;
	justify-content: space-between;
	animation: fadeIn 0.4s;
}

#close-modal,
#countdown {
	width: 100%;
	height: 50px;
	background-color: #ffc61b;
	display: none;
	align-items: center;
	justify-content: center;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	transition: 0.3s;
}

#close-modal:hover {
	cursor: pointer;
	background-color: #a10d3e;
	color: whitesmoke;
}

#welcome-modal,
#about-modal,
#solutions-modal,
#contact-modal {
	width: 100%;
	display: none;
	flex-direction: column;
	align-items: center;
}

#contact-modal,
#welcome-modal {
	height: 270px;
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
